<template>
  <div>
    <main class="content container order-info" v-if="order">
      <div class="content__top">
        <h1 class="content__title">
            <span> {{ order?.id }}</span>
        </h1>
      </div>
      <section class="cart">
        <form class="cart__form form" action="#" method="POST">
          <div class="cart__field">
            <p class="cart__message">
               &nbsp;  . &nbsp;    &nbsp; .
                 &nbsp; &nbsp;     .
            </p>
            <ul class="dictionary">
              <li class="dictionary__item">
                <span class="dictionary__key">
                  
                </span>
                <span class="dictionary__value">
                  {{ order?.name }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">
                   
                </span>
                <span class="dictionary__value">
                  {{ order?.address }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">
                  
                </span>
                <span class="dictionary__value">
                  {{ order?.phone }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">
                  Email
                </span>
                <span class="dictionary__value">
                  {{ order?.email }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">
                   
                </span>
                <span class="dictionary__value">
                  {{ order?.deliveryType.title }}
                </span>
              </li>
            </ul>
          </div>
          <div class="cart__block">
            <ul class="cart__orders">
              <li class="cart__order" v-for="item in order.basket.items" :key="item.id">
                <h3>{{ item.product.title }}</h3>
                <b>{{ item.quantity * item.product.price | numberFormat }}  <span v-if="item.quantity > 1">({{ item.quantity }})</span></b>
                <p class="cart__order--info">
                <span>: {{ item.product.id }}</span>
                <span>: {{ item.color.color.title }} <i :class="{'cart__order--white' : item.color.color.code === '#ffffff'}" :style="`background-color: ${item.color.color.code}`"></i></span>
                <span>: {{ item.size.title }}</span>
                </p>
              </li>
            </ul>
            <div class="cart__total">
              <p>: <b v-if="order?.deliveryType.price > 0">{{ order?.deliveryType.price }} </b>
                <b v-else></b>
              </p>
              <p>: <b>{{ order?.basket.items.length }} {{ $declension(order?.basket.items.length, ['', '', '']) }}</b>    <b>{{ totalPriceWithDelivery | numberFormat }} </b></p>
            </div>
          </div>
        </form>
      </section>
    </main>
    <main class="container" v-else>
      <h2>   :(</h2>
    </main>
  </div>
</template>

<script>
import numberFormat from '@/helpers/numberFormat'

export default {
  filters: {
    numberFormat
  },
  metaInfo () {
    return {
      title: '   ' + this.order?.id ? this.order?.id : '...' + ' / Moire'
    }
  },
  created () {
    if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
      return
    }
    this.$store.dispatch('loadOrderInfo', this.$route.params.id)
  },
  computed: {
    order () {
      return this.$store.state.orderInfo
    },
    totalPriceWithDelivery () {
      return this.$store.state.orderInfo.basket.items
        .reduce((acc, item) => (item.price * item.quantity) + acc, parseInt(this.$store.state.orderInfo.deliveryType.price))
    }
  }
}
</script>
